.sub-title {
  font-family: "Open", sans-serif;
  font-size: 1.4rem;
}

.container {
  left: 50%;
  top: 35%;
  min-width: 375px;
  margin: 0 auto;
  position: absolute;
  transform: translate(-50%);
}

h1 {
  font-family: "Open", sans-serif;
  font-weight: 700;
  font-size: 1.9em;
  font-weight: 400;
  margin-top: 0;
  letter-spacing: 2px;
}

.sub-title {
  text-align: center;
}

@media (min-width: 768px) {
  h1 {
    text-align: center;
    font-size: 4rem;
  }

  .sub-title {
    font-family: "Open", sans-serif;
    font-size: 1.8rem;
  }
}
